@import url('./_inactive/popup__button_inactive.css');

.popup__button {
width: 100%;
height: 50px;
cursor: pointer;
color: #FFF;
font-weight: normal;
font-size: 18px;
line-height: 22px;
padding: 0;
border: 0;
transition: all .3s;
background-color: #000;
}

.popup__button:hover {
  opacity: .8;
}

@media screen and (max-width: 500px) {
  .popup__button {
    font-size: 14px;
    line-height: 17px;
}
}
