.popup__close {
 position: absolute;
 top: -40px;
 right: -40px;
 cursor: pointer;
 transition: all .3s;
 border: 0;
 padding: 0;
 background-color: rgba(0, 0, 0, 0);
 background-position: center;
 background-image: url('./../../../images/Close-Icon.svg');
 width: 40px;
 height: 40px;
}

.popup__close:hover {
  opacity: .6;
}
