@keyframes fade-out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.popup {
 background-color: rgba(0, 0, 0, .5);
 position: fixed;
 top: 0;
 left: 0;
 width: 100%;
 height: 100%;
display: none;
 justify-content: center;
 align-items: center;
 animation: fade-out .4s;
}
