.popup__container {
 max-width: 430px;
 width: 100%;
 background-color: #FFF;
 box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
 border-radius: 10px;
 position: relative;
 padding: 34px 36px 36px;
 box-sizing: border-box;
}

@media screen and (max-width: 450px) {
  .popup__container {
    max-width: 240px;
}
}
